import React, {FC} from "react";
import './styles/styles.scss';

export type MainBannerProps = {
    title: string;
    description?: string;
    buttonText: string;
    image?: string;
};

export const MainBanner: FC<MainBannerProps> = ({title,description, buttonText, image }) => (
    <div className="mainBanner">
        <div className="mainBanner__Content">
            <h1 className="mainBanner__Title">
                {title}
            </h1>
            <h3 className="mainBanner__Description">
                {description}
            </h3>
            <a className="mainBanner__CatalogLink">
                {buttonText}
            </a>
        </div>
        {
            image &&
            <div className="mainBanner__ImageWrap">
                <img decoding="async" loading="lazy" src={image} width={1440} height={665}/>
            </div>
        }

    </div>
);
