import React, {FC, MouseEvent, useEffect, useState} from "react";
import * as S from "./MainThemesList.styles";
import './styles/styles.scss';

// Components
import {ThemeItemProps, ThemeItem} from "../ThemeItem/ThemeItem";

// Libs

import {MainThemeEllipse, MainThemeEllipseProps} from "./MainThemeEllipse/MainThemeEllipse";
import {idGenerator} from "../../lib/idGenerator";
import {MainThemeEllipseList} from "../../mock/mainThemeList";

// Mock
export type MainThemesListProps = {
    items: ThemeItemProps[];
};

export type MouseCoordinateTypes = {
    clientY: number;
    clientX: number;
};

export const MainThemesList: FC<MainThemesListProps> = ({
    items
}) => {
    const [mouseCoordinate, setMouseCoordinate] = useState<MouseCoordinateTypes | undefined>(null);

    useEffect(() => {
        window.addEventListener("scroll", onScrollHandler);
    }, [])

    const onScrollHandler = () => {
        const value = Number(window.pageYOffset);
        setMouseCoordinate({clientY: -value / 3, clientX: value/ 3});
    }

    return (
        <div className="mainThemesList">
            <div className="mainThemesList__Content" >
                {
                    items.map(item => (
                        <ThemeItem
                            {...item}
                            key={idGenerator()}
                        />
                    ))
                }
            </div>
            <div className="mainThemesList__Background">
                {
                    MainThemeEllipseList.map((item) => (
                        <MainThemeEllipse {...item}/>
                    ))
                }
            </div>
        </div>
    );
}

