import React, {FC, useEffect, useState} from "react";
import './styles/styles.scss';
import {MouseCoordinateTypes} from "../MainThemesList";

export type MainThemeEllipseProps = {
    circleWidth?: number;
    circleHeight?: number;
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
    color?: string;
    deg?: number;
    borderWidth?: number;
    borderStyle?: string;
};

export const MainThemeEllipse: FC<MainThemeEllipseProps> = ({
    circleWidth,
    circleHeight,
    top,
    bottom,
    left,
    right,
    color= "#000",
    deg= 0,
    borderWidth = 1,
    borderStyle = "solid",
    children,
}) => {

    const styles = {
        height: circleHeight,
        width: circleWidth,
        top,
        bottom,
        left,
        right,
        borderColor: "#000",
        borderWidth: "1px",
        borderStyle: "solid",
        transform: `${deg ? `rotate(${deg}deg)` : `rotate(0)`}`,
    }

    return (
        <div className="mainThemeEllipse"
            style={{...styles}}
        >
            {
                (children) ? children : ``
            }
        </div>
    );
}