import React, { FC } from "react";

export type IconProps = {
    iconId: string;
    className?: string;
    width?: string | number;
    height?: string | number;
    alt?: string;
};

export const Icon: FC<IconProps> = ({ iconId, className = `icon`, width = 24, height = 24, alt, ...props }) => {
    const attrs: { alt?: string; ariaHidden?: string } = {};
    if (!!alt) attrs.alt = alt;
    else attrs[`aria-hidden`] = `true`;

    return (
        <svg className={className} width={width} height={height} {...attrs} {...props}>
            <use xlinkHref={`#${iconId}`} />
        </svg>
    );
};
