import React, { FC, useState } from "react"
import './styles/styles.scss';
import {MainBanner} from "../MainBanner/MainBanner";
import {mainBanner} from "../../mock/mainBanner";
import {OurProducts} from "../OurProducts/OurProducts";
import {ourProductsList, ourProductsTabs} from "../../mock/ourProducts";
import {MainThemesList} from "../MainThemesList/MainThemesList";
import {mainThemeList} from "../../mock/mainThemeList";

export type MainProps = {

};

export const Main: FC<MainProps> = ({}) => {


  return (
    <main className="main">
      <MainBanner {...mainBanner} />
      <OurProducts tabsList={ourProductsTabs} items={ourProductsList}/>
      <MainThemesList items={mainThemeList}/>
    </main>
  );
}
