import React, {FC, useEffect} from "react";
import './styles/styles.scss';

import ArrowIcon from "/src/images/moreArrow.svg";
import {Icon} from "../Icon/Icon";

export type ThemeItemProps = {
    IMAGE: string;
    TITLE: string;
    DESCRIPTION: string;
    LINK: string;
    leftSide?: boolean;
    buttonText: string;
    SECOND_IMAGE?: string;
    absoluteTitle?: boolean;

};

export const ThemeItem: FC<ThemeItemProps> = ({
  IMAGE,
  TITLE,
  DESCRIPTION,
  LINK,
  leftSide,
  buttonText,
  SECOND_IMAGE,
  absoluteTitle,

}) => {


    return (
        <div className="themeItem">
            <div className={`themeItem__Content ${leftSide ? `leftSide` : ``}`}>
                <div className="themeItem__ImageWrap">
                    {
                        IMAGE && <img decoding="async" loading="lazy" src={IMAGE} />
                    }
                </div>
                <div className="themeItem__TextBlock">
                    <h2 className={`themeItem__Title ${absoluteTitle ? `absoluteTitle` : ``}`}>
                        {
                            TITLE
                        }
                    </h2>
                    <div className="themeItem__Desciprtion">
                        <p>
                            {
                                DESCRIPTION
                            }
                        </p>
                    </div>
                    <a className="themeItem__MoreLink" href={LINK}>
                        <span>{buttonText}</span> <Icon iconId={ArrowIcon.id} width={24} height={24} />
                    </a>
                    {
                        SECOND_IMAGE &&
                        <div className="themeItem__SecondImage">
                            <img decoding="async" loading="lazy" src={SECOND_IMAGE} />
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}